    position: relative;
    text-align: left;
  }
  .ui-slider .ui-slider-handle {
    position: absolute;
    z-index: 2;
    width: 1.8em;
    height: 1.8em;
    cursor: default;
    -ms-touch-action: none;
    touch-action: none;
